.fullScreenWr {
  width: 100%;
  border-bottom: 1px solid #6b6b6b;
}

.wrapper {
  max-width: 1465px;
  margin: 0 auto;
}

.container {
  height: 115px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
}

.logoContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.logo {
  margin: auto 0;
  margin-right: 10px;
}

.logoText {
  font-size: 40px;
  margin: auto 0;
}

.header1 {
  margin: auto 0;
  width: 150px;
  font-size: 24px;
}

.contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin: auto 0;
}

.tel,
.mail {
  font-family: Cambria, "Playfair Display", serif;
  color: inherit;
  text-decoration: inherit;
  padding: 5px;
  -webkit-transition: color 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
}
.tel:hover,
.mail:hover {
  cursor: pointer;
  -webkit-box-shadow: inset 210px 0 0 0 #e9e9e9;
  box-shadow: inset 210px 0 0 0 #e9e9e9;
}

.tel {
  font-size: 24px;
  font-weight: bold;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.mail {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.socials {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: auto 0;
}

.viber,
.whatsapp {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-text-decoration: indianred;
  text-decoration: indianred;
  color: inherit;
  -webkit-box-shadow: inset 0 0 0 0 #e9e9e9;
  box-shadow: inset 0 0 0 0 #e9e9e9;
  padding: 5px;
  -webkit-transition: color 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
}
.viber:hover,
.whatsapp:hover {
  cursor: pointer;
  -webkit-box-shadow: inset 125px 0 0 0 #e9e9e9;
  box-shadow: inset 125px 0 0 0 #e9e9e9;
}

.viber {
  margin-right: 11px;
}

.viberLogo,
.whatsappLogo {
  width: 28px;
  height: 28px;
  margin: auto 0;
}

.viberLogo,
.whatsappLogo {
  margin-right: 7px;
}

.viberLogoText,
.whatsappLogoText {
  margin: auto 0;
  font-family: Cambria, "Playfair Display", serif;
}

